@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  height: 100vh;
}

.App * {
  font-family: 'Press Start 2P', cursive;
}
/*# sourceMappingURL=App.css.map */
.Game {
  margin: auto;
  overflow: hidden;
  background-color: #b2f23a;
  background: radial-gradient(#b2f23a, #76a027);
  min-width: 400px;
  position: relative;
}

.Game canvas {
  width: 100%;
  border: 2px solid black;
  transform-origin: top left;
}
/*# sourceMappingURL=Game.css.map */
.Ui {
  width: 100%;
  border-left: 2px solid black;
  border-top: 2px solid black;
  border-right: 2px solid black;
}

.Ui button {
  padding: 10px;
  cursor: pointer;
  background-color: #000 !important;
  color: #b2f23a !important;
  border: 0;
  transition: all 0.2s;
}

.Ui button:hover {
  box-shadow: 4px 4px #76a027;
  transform: translate(-2px, -2px);
}

.Ui .ui-top {
  height: 25px;
}

.Ui .ui-top .ui-right {
  height: 100%;
  float: left;
  width: 50%;
  text-align: right;
  line-height: 25px;
}

.Ui .ui-top .ui-left {
  padding-left: 5px;
  height: 100%;
  float: left;
  width: 50%;
  text-align: left;
  line-height: 25px;
}

.Ui .ui-top .ui-left img {
  margin-right: 5px;
  width: 25px;
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -moz-crisp-edges;
      image-rendering: pixelated;
}

.Ui .health-bar {
  border-top: 2px solid black;
  height: 25px;
  position: relative;
}

.Ui .health-bar .fill {
  background-color: #000;
  height: 100%;
  overflow: hidden;
  -webkit-clip-path: inset(0 0 0 0);
          clip-path: inset(0 0 0 0);
}

.Ui .health-bar .fill.flashing {
  -webkit-animation: flash_hp 0.3s infinite alternate;
          animation: flash_hp 0.3s infinite alternate;
}

.Ui .health-bar .fill p {
  color: #b2f23a;
}

.Ui .health-bar p {
  position: absolute;
  margin: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #000;
}

.Ui .menu {
  position: absolute;
  display: inline-block;
  top: 50%;
  left: 50%;
  text-align: center;
  transform: translate(-50%, -50%);
  background-color: #b2f23a;
  border: 2px solid black;
  padding: 30px 40px;
}

.Ui .menu button {
  margin-top: 10px;
}

@-webkit-keyframes flash_hp {
  0% {
    background-color: #000;
  }
  100% {
    background-color: #363636;
  }
}

@keyframes flash_hp {
  0% {
    background-color: #000;
  }
  100% {
    background-color: #363636;
  }
}
/*# sourceMappingURL=Ui.css.map */
